"use client";

import { ArrowLeft } from "lucide-react";
import Link from "next/link";
import { Card } from "@/components/ui/card";

export default function DeThiPage() {
  const exams = [
    { title: "Đề 31/05/2025", date: "31/05/2025" },
    { title: "Đề HK2/2024–2025", date: "HK2/2024–2025" },
    { title: "Đề 18/11/2023", date: "18/11/2023" },
    { title: "Đề 13/11/2022", date: "13/11/2022" },
    { title: "Đề 29/05/2022", date: "29/05/2022" },
  ];

  return (
    <div className="flex h-screen bg-background text-foreground overflow-hidden" style={{ maxWidth: "416px", maxHeight: "496px" }}>
      <div className="flex-1 flex flex-col overflow-hidden">
        <header className="flex items-center justify-between p-2 border-b border-border bg-card">
          <Link href="/">
            <button className="p-1 hover:bg-accent rounded transition-colors" aria-label="Go back">
              <ArrowLeft size={18} />
            </button>
          </Link>
          <h1 className="text-sm font-semibold">Đề Thi</h1>
          <div className="w-4" />
        </header>

        <main className="flex-1 overflow-y-auto p-1.5 space-y-1.5">
          {exams.map((exam, index) => (
            <Card key={index} className="p-2 bg-card border border-border hover:bg-accent transition-colors cursor-pointer">
              <h3 className="text-xs font-semibold text-foreground">{exam.title}</h3>
              <p className="text-[10px] text-muted-foreground mt-0.5">{exam.date}</p>
            </Card>
          ))}
        </main>

        <footer className="p-1.5 border-t border-border bg-card text-center text-xs text-muted-foreground">
          Danh Sách Đề Thi
        </footer>
      </div>
    </div>
  );
}
